.n-1-icon {
  position: relative;
  border-radius: 200px;
  width: 300px;
  height: 300px;
  object-fit: cover;
}
.n-1-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.b {
  position: relative;
  display: inline-block;
  width: 368px;
  height: 46px;
  flex-shrink: 0;
}
.contaoner,
.header,
.mini {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.contaoner {
  width: 100%;
  margin: 0 !important;
  position: absolute;
  top: 0;
  left: calc(50% - 600.5px);
  padding: 24px 0;
  box-sizing: border-box;
  align-items: center;
  gap: 16px;
  max-width: 1200px;
  z-index: 0;
}
.header,
.mini {
  position: relative;
}
.header {
  align-self: stretch;
  background-color: #dd2800;
  height: 410px;
  align-items: flex-start;
}
.mini {
  background-color: #f5f5f5;
  width: 100%;
  align-items: center;
  gap: 40px;
  text-align: center;
  font-size: 36px;
  color: #fff;
  font-family: Inter;
}
