.image-icon1 {
  align-self: stretch;
  max-width: 100%;
  overflow: hidden;
  height: 320px;
  flex-shrink: 0;
  object-fit: cover;
}
.sed-elit-gravida {
  flex: 1;
  position: relative;
  display: -webkit-inline-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 25;
  -webkit-box-orient: vertical;
  mix-blend-mode: normal;
}
.sed-elit-gravida-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.turpis-aliquet-et {
  align-self: stretch;
  flex: 1;
  position: relative;
  font-size: 16px;
  color: #000;
  display: -webkit-inline-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 76;
  -webkit-box-orient: vertical;
  min-height: 80px;
  max-height: 96px;
}
.frame-div {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 11px;
}
.vector-icon5 {
  position: relative;
  width: 32px;
  height: 32px;
}
.vector-group {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.div1 {
  position: relative;
  font-weight: 900;
}
.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.frame-container,
.frame-parent1 {
  align-self: stretch;
  display: flex;
  justify-content: space-between;
}
.frame-parent1 {
  flex-direction: row;
  align-items: center;
  text-align: right;
  font-size: 36px;
  color: #8a8a8a;
}
.frame-container {
  flex: 1;
  flex-direction: column;
  padding: 16px;
  align-items: flex-start;
}
.card1,
.food {
  display: flex;
  justify-content: flex-start;
}
.card1 {
  flex: 1;
  border-radius: 24px;
  background-color: #fff;
  box-shadow: 156px 278px 89px transparent, 100px 178px 82px rgba(0, 0, 0, 0.01),
    56px 100px 69px rgba(0, 0, 0, 0.05), 25px 44px 51px rgba(0, 0, 0, 0.09),
    6px 11px 28px rgba(0, 0, 0, 0.1);
  height: 547px;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  min-width: 300px;
  max-width: 560px;
}
.food {
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 16px;
  box-sizing: border-box;
  align-items: center;
  gap: 16px;
  max-width: 1200px;
  text-align: left;
  font-size: 24px;
  color: #4f4f4f;
  font-family: Inter;
}
